* { outline-color: #1DA1F2; margin: 0; padding: 0; }
button { 
  appearance: none;
  background-color: transparent;
  border-width: 0; 
}
input[type=checkbox]{
  vertical-align: middle;
  appearance: none;
  cursor: pointer;
  position: relative;
  border-width: 0;
  width: 15px;
  height: 15px;
}

input[type=checkbox]::after{
  position: absolute;
  top: 0; right: 0; bottom: 0; left: 0;
  border: 1px solid #1DA1F2;
  text-align: center;
  line-height: 15px;
  content: ' ';
}
     
input[type=checkbox]:checked::after{
  background-color: #1DA1F2;
  color: #E8F5FE;
  content: "âœ“";
  font-size: 12px;
  font-weight: bold;
}

input[type=radio] {
  vertical-align: text-bottom;
  appearance: none;
  cursor: pointer;
  position: relative;
  border-width: 0;
  width: 13px;
  height: 13px;
  margin: 0 2px;
}
input[type=radio]::before{
  position: absolute;
  top: 0; right: 0; bottom: 0; left: 0;
  border-radius: 50%;
  border: 1px solid #1DA1F2;
  text-align: center;
  content: ' ';
}
     
input[type=radio]:checked::after{
  position: absolute;
  top: 2px; right: 2px; bottom: 2px; left: 2px;
  border-radius: 50%;
  background-color: #1DA1F2;
  text-align: center;
  content: ' ';
}
/* todo-form */
.todo-form {
  margin: auto;
  width: 300px;
  margin-top: 20px;
  position: relative;
  text-align: left;
}
.todo-form input {
  box-sizing: border-box;
  width: 100%;
  border: 1px solid #E1E4E5;
  line-height: 40px;
  border-radius: 4px;
  padding: 0 35px 0 10px;
}
.todo-form button {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  color: #E1E4E5;
  position: absolute;
  right: 5px;
  top: 5px;
}
.todo-form input:focus + button {
  color: #1DA1F2;
}
.todo-form button:hover {
  color: #1DA1F2;
  background-color: #E8F5FE;
}
/* todo-list */
.todo-list {
  list-style: none;
  border-radius: 4px;
  box-shadow: 0 4px 6px rgba(0,0,0,.2);
  max-width: 500px;
  min-width: 300px;
  margin: 20px auto;
  padding-left: 0px;
}

.todo-list::before {
  content: '';
  display: table;
}

/* todo-item */
.todo-item {
  margin: 10px;
  border-radius: 4px;
  border: 1px solid #E1E4E5;
  text-align: left;
  font-size: 17px;
  font-weight: 700;
  color: #282c34;
  line-height: 40px;
  transition: all 0.3s;
  padding-left: 10px;
}

.todo-item:hover {
  background-color: #E7EAEC;
}

.todo-item span {
  margin-left: 4px;
}

.todo-item input:checked + span {
  color: #1DA1F2;
  text-decoration: line-through;
}

/* todo-list-bottombar */
.todo-list > div {
  background-color: #E8F5FE;
  padding: 0 10px;
  text-align: left;
  line-height:20px;
  color: #A9ABAC;
  font-size: 12px;
}

.todo-list .todo-filter {
  float: right;
}

